<template>
  <div class="the-landing-smart-contract" id="sourceCode">
    <div class="the-landing-smart-contract__section-wrapper">
      <div class="the-landing-smart-contract__list">
        <div class="the-landing-smart-contract__list-sidebar">
          <div class="the-landing-smart-contract__title">
            Source <span class="highlight">Code</span>
          </div>
        </div>
        <div class="the-landing-smart-contract__list-content-wrapper">
          <div class="the-landing-smart-contract__headline">
            Smart Contracts
          </div>

          <div class="the-landing-smart-contract__list-content">
            <div class="the-landing-smart-contract__list-item">
              <div class="the-landing-smart-contract__list-icon">
                <e-icon :src="iconCustody"/>
              </div>

              <div class="the-landing-smart-contract__list-title">
                Custody
                <span class="the-landing-smart-contract__list-title-label">Base</span>
              </div>

              <div class="the-landing-smart-contract__list-item-content">
                This is the main custody smart contract for DMEX. All user funds are stored here. Deposits and withdrawals are performed from this contract.
              </div>

              <div class="the-landing-smart-contract__list-links">
                <a href="https://github.com/dmex-app/smart-contracts/blob/master/base.sol" target="_blank" class="the-landing-smart-contract__list-link">
                  <e-icon :src="iconLogoGithub" />
                  GitHub
                </a>
              </div>
            </div>
            <div class="the-landing-smart-contract__list-item">
              <div class="the-landing-smart-contract__list-icon">
                <e-icon :src="iconContractTrading"/>
              </div>

              <div class="the-landing-smart-contract__list-title">
                Trading
              </div>

              <div class="the-landing-smart-contract__list-item-content">
                Trades are performed on this contract. It stores the futures contracts, order fills information and performs collateral and funding calculations. Calls the Cusotdy Contract to move balances between users.
              </div>

              <div class="the-landing-smart-contract__list-links">
                <a href="https://github.com/dmex-app/smart-contracts/blob/master/futures.sol" target="_blank" class="the-landing-smart-contract__list-link">
                  <e-icon :src="iconLogoGithub" />
                  GitHub
                </a>
              </div>
            </div>
            <div class="the-landing-smart-contract__list-item">
              <div class="the-landing-smart-contract__list-icon">
                <e-icon :src="iconContractOracle"/>
              </div>

              <div class="the-landing-smart-contract__list-title">
                Oracle
              </div>

              <div class="the-landing-smart-contract__list-item-content">
                The sole purpose of this smart contract is to provide the settlement price from the price source. It calls the <a href="https://provable.xyz">Provable.xyz</a> decentralized Oracle contract requesting the latest price for an asset.
              </div>

              <div class="the-landing-smart-contract__list-links">
                <a href="https://github.com/dmex-app/smart-contracts/blob/master/oracle.sol" target="_blank" class="the-landing-smart-contract__list-link">
                  <e-icon :src="iconLogoGithub" />
                  GitHub
                </a>
              </div>
            </div>
          </div>

          <div class="the-landing-smart-contract__headline">
            Website
          </div>
          <div class="the-landing-smart-contract__list-content">
            <div class="the-landing-smart-contract__list-item">
              <div class="the-landing-smart-contract__list-icon" style="width: 108px; height: 127px;">
                <e-icon :src="iconWebApp"/>
              </div>

              <div class="the-landing-smart-contract__list-title">
                Web App
              </div>

              <div class="the-landing-smart-contract__list-item-content">
                The DMEX Web Application is an open source software that acts as a connector between the user and the smart contracts. It never sends your private keys to our servers or anywhere else. All information is stored in your browser and deleted with your cache.
              </div>

              <div class="the-landing-smart-contract__list-links">
                <a href="https://github.com/dmex-app/frontend" target="_blank" class="the-landing-smart-contract__list-link">
                  <e-icon :src="iconLogoGithub" />
                  GitHub
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import EIcon from '@/components/EIcon';

import iconCustody from '@/icons/e-icon__custody.svg';
import iconContractTrading from '@/icons/e-icon__contract-trading.svg';
import iconContractOracle from '@/icons/e-icon__contract-oracle.svg';
import iconWebApp from '@/icons/e-icon__web-app.svg';
import iconLogoGithub from '@/icons/e-icon__logo-github.svg';
import iconEtherscan from '@/icons/e-icon__etherscan.svg';
import iconBack from '@/icons/e-icon__back-bold.svg';

export default {
  name: 'the-landing-smart-contract',

  data() {
    return {
      // Icons
      iconCustody,
      iconContractTrading,
      iconContractOracle,
      iconWebApp,
      iconLogoGithub,
      iconEtherscan,
      iconBack,
    };
  },

  methods: {
  },

  components: {
    EIcon,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-landing-smart-contract {
  $this: &;

  position: relative;
  background: #ECECEF;
  /*border-radius: 40px 40px 0 0;
  margin-top: -40px;*/
  padding: 80px 0;
  box-shadow: inset 0 10px 12px 0 rgba(1,8,62,0.10);

  @include r(992) {
    padding: 60px 0;
  }

  @include r(767) {
    padding: 40px 0 60px;
  }

  &__section-wrapper {
    width: 100%;
    max-width: 1280px;
    padding: 0 30px;
    box-sizing: border-box;
    margin: 0 auto;
    position: relative;

    @include r(767) {
      padding: 0 15px;
    }
  }

  &__headline {
    display: inline-block;
    background: rgba(#C4CCEE, .3);
    font-weight: 500;
    font-size: 22px;
    color: #6B7397;
    letter-spacing: 0;
    line-height: 25px;
    padding: 15px 20px;
    border-radius: 12px;
    margin-bottom: 44px;
    text-transform: uppercase;
    margin-left: -20px;
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    justify-content: space-between;

    @include r(992) {
      justify-content: center;
    }
  }

  &__list-sidebar {
    flex: 0 0 290px;

    @include r(992) {
      flex: 0 0 100%;
      margin-bottom: 30px;
      text-align: center;
    }
  }

  &__title {
    font-weight: 500;
    font-size: 36px;
    color: #112271;
    letter-spacing: 0;
    line-height: 46px;

    @include r(767) {
      font-size: 30px;
      line-height: 39px;
    }
  }

  .highlight {
    color: #00DB9C;
  }

  &__list-content-wrapper {
    flex: 1;
    margin-left: 100px;

    @include r(1100) {
      margin-left: 70px;
    }

    @include r(992) {
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      flex: 0 0 100%;
      margin-left: 0;
      text-align: center;
    }
  }

  &__list-content {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    justify-content: space-between;
    margin-top: -80px;
    padding-bottom: 60px;
    /*margin-bottom: 60px;*/
    /*border-bottom: 1px solid rgba(#112271, .1);*/

    &:last-child {
      padding-bottom: 0;
      margin-bottom: 0;
      border-bottom: none;
    }

    @include r(992) {
      max-width: 550px;
    }
  }

  &__list-item {
    display: flex;
    flex-direction: column;
    margin-top: 80px;

    &:nth-child(2n + 1) {
      flex: 0 0 calc(45% - 40px);
    }

    &:nth-child(2n + 2) {
      flex: 0 0 calc(55% - 40px);
    }

    @include r(1100) {
      flex: 0 0 100% !important;
    }

    @include r(992) {
      justify-content: center;
      align-items: center;
    }
  }

  &__list-title {
    display: flex;
    align-items: center;
    font-weight: 500;
    font-size: 23px;
    color: #112271;
    letter-spacing: -.5px;
    line-height: 25px;
  }

  &__list-title-label {
    display: inline-block;
    border-radius: 5px;
    background: #0122E7;
    font-weight: 500;
    font-size: 13px;
    color: #FFFFFF;
    letter-spacing: 0;
    line-height: 15px;
    padding: 5px 6px;
    margin-left: 12px;
  }

  &__list-icon {
    width: 120px;
    height: 92px;
    margin-bottom: 20px;
    display: flex;
    align-items: center;
  }

  &__list-item-content {
    font-weight: 500;
    font-size: 15px;
    color: #6B7397;
    letter-spacing: 0;
    line-height: 24px;
    margin: 20px 0;

    a {
      font-weight: 500;
      font-size: 15px;
      color: #112271;
      letter-spacing: 0;
      line-height: 24px;
      text-decoration: none;
      padding-bottom: 3px;
      border-bottom: 2px solid rgba(0,219,156,0.40);
      transition: border-color .15s ease-in-out;

      &:hover {
        border-bottom: 2px solid rgba(0,219,156,1);
      }
    }
  }

  &__list-links {
    display: flex;
    align-items: center;
    margin-top: auto;
  }

  &__list-link {
    display: inline-flex;
    align-items: center;
    font-weight: 500;
    font-size: 14px;
    color: #6B7397;
    letter-spacing: 0;
    line-height: 26px;
    border-radius: 25px;
    height: 50px;
    border: 3px solid #FFFFFF;
    padding: 0 20px 0 8px;
    box-sizing: border-box;
    text-decoration: none;
    margin-right: 20px;
    transition: all .15s ease-in-out;

    &:hover {
      background: #FFFFFF;
      box-shadow: 0 12px 18px 0 rgba(17,34,113,0.05);

      .e-icon svg {
        fill: #0122E7;
      }
    }

    .e-icon {
      width: 30px;
      height: 30px;
      margin-right: 8px;

      svg {
        fill: #112271;
      }
    }

    &.etherscan .e-icon {
      width: 24px;
      height: 24px;
    }
  }

  &__back {
    margin-bottom: 48px;

    @include r(992) {
      margin-bottom: 30px;
    }
  }

  &__back-button {
    position: relative;
    display: flex;
    align-items: center;
    background-color: transparent;
    border: none;
    text-transform: uppercase;
    font-weight: 500;
    font-size: 12px;
    color: #112271;
    text-align: center;
    cursor: pointer;
    transition: color .15s ease-in-out;

    &:hover {
      color: #00DB9C;

      #{$this}__back-button-icon svg path {
        stroke: #00DB9C;
      }
    }
  }

  &__back-button-icon {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 22px;
    height: 22px;
    border-radius: 50%;
    border: 1px solid #00DB9C;
    margin-right: 12px;

    svg {
      position: relative;
      width: 7px;
      height: 10px;
      margin-left: -1px;

      path {
        transition: stroke .15s ease-in-out;
      }
    }
  }
}
</style>



