<template>
  <div
    class="page-inner"
    :class="{ 'is-popup-showing': isPopupShowing }"
  >
    <transition
      name="transition-header"
      :duration="{ enter: 2000, leave: 600 }"
    >
      <div
        v-if="isPopupShowing"
        class="page-news__header"
      >
        <the-header-inner/>
      </div>
    </transition>

    <div class="page-inner__root">
      <div id="page-inner__wrapp" ref="contentWrap" class="page-post__wrapp">
        <the-header-new />

        <div class="page-inner__main">
          <div class="page-inner__main-wrapper">
            <the-landing-smart-contract />
          </div>
        </div>

        <the-latest-blog-posts />

        <the-footer/>
      </div>
    </div>

    <!-- popup and menu -->
    <the-popup v-if="isPopupShowing"/>
    <the-menu-sidebar v-if="isPopupShowing"/>
    <!-- popup and menu -->
  </div>
</template>

<script>
/**
 * Component: Страница поста
 *
 * @version 2.0.0
 */


import { mapGetters } from 'vuex';
import iconArrow from '@/icons/e-icon_arrow.svg';
import iconBack from '@/icons/e-icon__back-big.svg';

// Components
import TheFooter from '@/containers/blocks/TheFooter';
import TheHeaderInner from '@/containers/blocks/TheHeaderInner';
import TheHeaderNew from '@/containers/blocks/TheHeaderNew';
import TheMenuSidebar from '@/containers/blocks/TheMenuSidebar';
import ThePopup from '@/containers/blocks/ThePopup';
import TheLatestBlogPosts from '@/containers/blocks/TheLatestBlogPosts';
import TheLandingSmartContract from '@/containers/blocks/TheLandingSmartContract';

export default {
  name: 'page-innerus',

  data() {
    return {
      iconArrow,
      iconBack,
    };
  },

  computed: {
    ...mapGetters({
      isPopupShowing: 'ui/isPopupShowing',
    }),
  },

  async mounted() {
    this.$ga.page(this.$router);
  },

  components: {
    TheFooter,
    TheHeaderInner,
    TheHeaderNew,
    TheMenuSidebar,
    ThePopup,
    TheLatestBlogPosts,
    TheLandingSmartContract,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.page-inner {
  $this: &;

  &__root {
    font-weight: 500;
    background-color: #fff;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    overflow-y: auto;
  }

  &__wrapp {
    overflow-y: auto;
    overflow-x: hidden;
    width: 100%;
    height: 100%;
  }

  &__header {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 78px;
    z-index: 102;
  }

  // MAIN section
  // ==================================================
  &__main {
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: #031472;
    position: relative;
    margin-top: -120px;
    padding-top: 120px;
    z-index: 0;

    &::after {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      z-index: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(to bottom, rgba(5,4,26,.5) 0%,rgba(5,4,26,.4) 30%, rgba(5,4,26,0) 70%, rgba(5,4,26,0) 100%);
    }

    &::before {
      content: '';
      position: absolute;
      left: 50%;
      transform: translate(-50%, 0);
      z-index: 0;
      bottom: -120px;
      width: 100%;
      max-width: 1600px;
      height: 180px;
      background: #030C52;
      border-radius: 50%;
      filter: blur(30px);
      opacity: .4;

      @include r(992) {
        display: none;
      }
    }
  }

  &__main-wrapper {
    background: #ECECEF;
    z-index: 1;
    padding-bottom: 40px;
  }

  &__main-content {
    width: 100%;
    max-width: 1440px;
    padding: 70px 130px 190px;
    box-sizing: border-box;
    margin: 0 auto;
    position: relative;
    display: flex;
    justify-content: space-between;

    @include r(1200) {
      padding: 70px 90px 150px;
    }

    @include r(992) {
      position: relative;
      padding: 50px 40px;
      flex-direction: column-reverse;
      justify-content: flex-start;
    }
  }

  &__main-article {
    width: 75%;
    margin-right: 120px;
    position: relative;

    @include r(1220) {
      margin-right: 60px;
    }

    @include r(992) {
      width: 100%;
    }

    @include r(420) {
      margin-right: 0;
      margin-bottom: 40px;
    }
  }

  &__heading {
    font-weight: normal;
    margin-bottom: 32px;
    position: relative;

    @include r(640) {
      margin-bottom: 26px;
    }
  }

  &__back {
    position: absolute;
    display: inline-block;
    width: 17px;
    height: 24px;
    cursor: pointer;
    left: -47px;
    top: 16px;

    @include r(768) {
      left: -32px;
    }

    @include r(640) {
      top: 11px;
    }

    &::before, &::after {
      display: block;
      position: absolute;
      content: '';
      width: 17px;
      height: 2px;
      background: #1f2ee9;
    }

    &::before {
      transform: rotate(-45deg);
      left: 0;
      top: 5px;
    }

    &::after {
      transform: rotate(45deg);
      left: 0;
      top: 16px;
    }
  }

  &__heading-main {
    color: #0d105b;
    font-size: 34px;
    font-weight: normal;
    margin-bottom: 20px;

    @include r(640) {
      line-height: 43px;
    }
  }

  &__heading-description {
    color: #000;
    font-size: 23px;
    line-height: 36px;
  }

  &__text {
    p {
      color: #000;
      opacity: 0.6;
      font-size: 16px;
      line-height: 24px;
      font-weight: normal;
    }
  }

  &__main-sidebar {
    padding-top: 19px;
    width: 25%;

    @include r(992) {
      position: static;
      transform: translateX(0);
      box-shadow: none;
      width: 100%;
      padding-top: 70px 0 0;
      background: transparent;
      height: auto;
    }
  }

  &__sidebar-post {
    margin-bottom: 16px;
  }

  &__sidebar-post-title {
    font-size: 16px;
    color: #1F2EE9;
    line-height: 21px;
    letter-spacing: 0.5px;
    transition: all 0.2s;
    cursor: pointer;
    margin-top: 5px;
    text-decoration: none;

    &.router-link-exact-active {
      color: #000;
    }

    &:hover {
      color: #000;
    }
  }
}

</style>



